
import HeaderCom from '@/components/common/HeaderCom'
import coInput from '@/components/common/Input'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'

import { mapMutations, mapActions } from 'vuex'
import validateForm from '@/untils/validate-form'
export default {
name: 'addRider',
components: {
HeaderCom,
coInput,
PopupPicker
},
data () {
return {
title: '添加乘车人',
cardTypeListStatus: false,
typeListStatus: false,

selectPassengerList: [['成人', '学生', '伤残军人']],
passengerList: ['成人','学生','伤残军人'],

selectCardList: [['身份证', '港澳通行证', '台湾护照','护照']],
idCardTypeList: ['身份证','港澳通行证','台湾护照','护照'],
selectTypeValue: ['成人'],
selectCardValue: ['身份证'],

yzForm: {
name: '请输入姓名',
type: '请选择乘客类型',
idCardType: '请选择证件类型',
idCard: '请输入证件号码'
},
riderForm: {
name: '',
type: '成人',
idCardType: '身份证',
idCard: ''
},
param:{
name: '',
type: 0,
idCardType: 0,
idCard: ''
}
}
},
computed: {
},
methods: {
...mapMutations('PERSON', [
'addRiderList'
]),




onChangeType (val) {
this.riderForm.type = val[0];
var index  = this.passengerList.indexOf(this.riderForm.type)
console.log(index)
this.param.type = index
},

onChangeCard (val) {
this.riderForm.idCardType = val[0];
var index  = this.idCardTypeList.indexOf(this.riderForm.idCardType)
console.log(index)
this.param.idCardType = index
},

addRider () {
var that = this
validateForm(this.yzForm, this.riderForm, () => {

this.param.name = this.riderForm.name
this.param.idCard = this.riderForm.idCard

this.$store.dispatch('PASSENGER/passengerSave', this.param).then(res => {
console.log(res)
if(res){
that.$loading.toast('新增成功', 1);
setTimeout(() => {
that.$router.goBack();
}, 1200)
}
})
})
}
},
mounted() {
},
}



