export default {
  name: 'coInput',
  components: {
  },
  props: {
    value: [String, Number],
    placeholder: {
      type: String,
      default: '请输入内容'
    },
    isClear: {
      type: Boolean,
      default: false
    },
    isDisable: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      currentValue: this.value === undefined || this.value === null
        ? ''
        : this.value,
    }
  },
  methods: {
    clearText () {
      this.currentValue = ''
      this.$emit("input", this.currentValue)
    }
  },
  watch: {
    currentValue (val, oldValue) {
      this.$emit("input", val)
    }
  },
  mounted () {
  }
}



